export default defineNuxtPlugin(() => {

    return {

        provide: {

            validateEmail: (email: string) => {
          
                if (/^\w+([\.-]?\w+)*@\w+([\.-]?\w+)*(\.\w{2,3})+$/.test(email)) 
                return true;
 
                return false;
  
            }
        }
    }
})
